#gutenify-settings-app {
	.gutenify-setting-panel-notice-area {
		margin: 10px 0 20px 0;
	}

	.gutenify-setting-panel-toolbar-area {
		padding: 10px 20px;
	}

	.gutenify-text-warning {
		color: #dca606;
	}

	.gutenify-text-success {
		color: #4caf50;
	}

	.gutenify-text-danger {
		color: #e91e63;
	}
}
